. Style\n */\n.uk-close {\n  /* 1 */\n  -webkit-appearance: none;\n  /* 2 */\n  margin: 0;\n  /* 3 */\n  border: none;\n  /* 4 */\n  overflow: visible;\n  /* 5 */\n  font: inherit;\n  color: inherit;\n  /* 6 */\n  text-transform: none;\n  /* 7. */\n  padding: 0;\n  background: transparent;\n  /* 8 */\n  display: inline-block;\n  box-sizing: content-box;\n  width: 20px;\n  line-height: 20px;\n  text-align: center;\n  vertical-align: middle;\n  opacity: 0.3;\n}\n/* Icon */\n.uk-close:after {\n  display: block;\n  content: \"\\f00d\";\n  font-family: FontAwesome;\n}\n/*\n * Hover\n * 1. Apply hover style also to focus state\n * 2. Remove default focus style\n * 3. Required for `a` elements\n */\n.uk-close:hover,\n.uk-close:focus {\n  opacity: 0.5;\n  /* 2 */\n  outline: none;\n  /* 3 */\n  color: inherit;\n  text-decoration: none;\n  cursor: pointer;\n}\n/* Modifier\n ========================================================================== */\n.uk-close-alt {\n  padding: 2px;\n  border-radius: 50%;\n  background: #eee;\n  opacity: 1;\n}\n/* Hover */\n.uk-close-alt:hover,\n.uk-close-alt:focus {\n  opacity: 1;\n}\n/* Icon */\n.uk-close-alt:after {\n  opacity: 0.5;\n}\n.uk-close-alt:hover:after,\n.uk-close-alt:focus:after {\n  opacity: 0.8;\n}\n/* ========================================================================\n   Component: Badge\n ========================================================================== */\n.uk-badge {\n  display: inline-block;\n  padding: 0 5px;\n  background: #00a8e6;\n  font-size: 10px;\n  font-weight: bold;\n  line-height: 14px;\n  color: #fff;\n  text-align: center;\n  vertical-align: middle;\n  text-transform: none;\n}\n/*\n * Keep color when badge is a link\n */\na.uk-badge:hover {\n  color: #fff;\n}\n/* Modifier: `uk-badge-notification`;\n ========================================================================== */\n.uk-badge-notification {\n  box-sizing: border-box;\n  min-width: 18px;\n  border-radius: 500px;\n  font-size: 12px;\n